import React from 'react';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import TabBar from './TabBar';

const blockName = 'tabs-tab-bar';

const SubTabList = aggregatedClasses('div')(blockName, 'subtabs-list');

const SubTabBar = ({ tabs = [], activeTab = '', onTabClick = () => null }) => (
  <TabBar
    activeTab={activeTab}
    onTabClick={onTabClick}
    renderCustomTabBar={({ tabsComponents, innerRef }) => (
      <SubTabList innerRef={innerRef}>{tabsComponents}</SubTabList>
    )}
    showSelectionIndicator={false}
    showSeparator
    tabs={tabs}
  />
);

export default SubTabBar;
